.landing-features-section {
  display: flex;
  justify-content: center;

  background-color: #171717;
  width: 100%;

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    max-width: 144rem;
    width: 100%;
    height: 35rem;
  }
}

.features-title {
  font-size: 2.8rem;
}

.feature-imgs {
  display: flex;
  justify-content: space-between;

  width: 100%;

  &__img {
    width: 26rem;
    height: 26rem;
    object-fit: cover;
    image-rendering: crisp-edges;
    border: 0.4rem grey solid;
  }
}
