@import url(https://fonts.googleapis.com/css?family=Courier+Prime&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}body{box-sizing:border-box;font-size:1.6rem}h1{margin:0}ul{list-style:none}a{color:inherit;text-decoration:none;cursor:pointer}button{border:0;outline:0;cursor:pointer}button:-moz-focus-inner{border:0}button:-moz-focusring{outline:0}input[type='search']::-webkit-search-decoration,input[type='search']::-webkit-search-cancel-button,input[type='search']::-webkit-search-results-button,input[type='search']::-webkit-search-results-decoration{display:none}.app{background-color:#1d1d1d;font-family:Arial, Helvetica, sans-serif;color:white}.main{min-height:calc(100vh - 3.6rem);width:100%}.react-tooltip-lite{background-color:#2d2d2d;border-radius:0.4rem;color:white;font-family:Arial, Helvetica, sans-serif;font-size:1.4rem;border:1px solid #666}.react-tooltip-lite-arrow{border-color:#666}.tooltip-shortcut{color:gold;text-transform:capitalize}

.header{display:grid;grid-template-columns:1fr 1fr 1fr;align-items:center;height:3.6rem;background-color:#2d2d2d;padding:0 2rem}.header__logo{font-family:'Courier Prime', monospace}.header__project-name{overflow:hidden;text-align:center;font-size:1.6rem;font-weight:normal}.header__button-wrapper{display:flex;justify-content:flex-end}.header__user-name{display:flex;align-items:center;margin-right:2rem}

.auth-button{background-color:#ffd700;border-radius:0.2rem;padding:0.6rem 1rem;margin:0 0.5rem;font-size:1.4rem;color:black;-webkit-transition:all 0.4s;transition:all 0.4s}.auth-button:hover{background-color:white}

.landing-wrapper{display:flex;flex-direction:column;align-items:center}

.landing-title-section{display:grid;grid-template-columns:1fr 1fr;grid-gap:5rem;gap:5rem;padding:5rem;max-width:144rem;background-color:#4f4f4f;color:white}.title-block{display:flex;flex-direction:column;justify-content:space-between}.title-block__title{font-size:6rem}.title-block__text{font-size:2.8rem;line-height:5rem}

.create-sprite-button{height:5rem;width:100%;padding:1rem;border-radius:0.2rem;background-color:gold;color:black;-webkit-transition:all 0.2s;transition:all 0.2s}.create-sprite-button:hover{background-color:white}

.landing-examples-section{display:flex;flex-direction:column;justify-content:space-evenly;max-width:144rem;width:100%;height:35rem}.examples-title{font-size:2.8rem}.example-sprites{display:flex;justify-content:space-between;width:100%}.example-sprites__img{width:26rem;height:26rem;object-fit:cover;image-rendering:crisp-edges;border:0.4rem grey solid}

.landing-features-section{display:flex;justify-content:center;background-color:#171717;width:100%}.landing-features-section__wrapper{display:flex;flex-direction:column;justify-content:space-evenly;max-width:144rem;width:100%;height:35rem}.features-title{font-size:2.8rem}.feature-imgs{display:flex;justify-content:space-between;width:100%}.feature-imgs__img{width:26rem;height:26rem;object-fit:cover;image-rendering:crisp-edges;border:0.4rem grey solid}

.landing-about-section{display:flex;justify-content:center;background-color:#111;width:100%}.landing-about-section__wrapper{max-width:144rem;width:100%;padding:2rem}.landing-about-section__wrapper a{text-decoration:underline}

.project-wrapper{position:relative;display:grid;grid-template-columns:9rem 17rem auto 25.6rem;grid-template-rows:100%;grid-gap:0.8rem;gap:0.8rem;align-items:center;height:calc(100vh - 3.6rem);padding:0.8rem}

.workbench{display:flex;justify-content:center;align-items:center;height:100%;width:100%;background-color:#a0a0a0}.canvas{background:url(data:image/png;base64,) repeat}

.tool-panel{width:9rem;padding:0.2rem}.tool-panel__tools{display:grid;justify-content:center;align-items:center;grid-template-columns:4rem 4rem;grid-template-rows:4rem 4rem;grid-gap:.2rem;gap:.2rem;margin-top:.8rem}.tool-panel__colors{display:flex;justify-content:center;align-items:center;margin-top:.5rem;width:9rem;height:9rem}

.stroke-size-wrapper{display:flex;justify-content:space-evenly;padding:0.1rem;height:2rem;pointer-events:all}.stroke-size-wrapper__tooltip{pointer-events:none;text-align:center}.stroke-size{display:flex;justify-content:center;align-items:center;width:2rem;height:2rem;border:0.2rem solid #444;cursor:pointer}.stroke-size::before{content:'';display:block;background-color:white}.stroke-size:hover{border-color:#888}.stroke-size--1::before{width:30%;height:30%}.stroke-size--2::before{width:45%;height:45%}.stroke-size--3::before{width:60%;height:60%}.stroke-size--4::before{width:75%;height:75%}.stroke-size--selected{border-color:gold}.stroke-size--selected::before{background-color:gold}.stroke-size--selected:hover{border-color:gold}

.tool-button{width:4rem;height:4rem;cursor:pointer;pointer-events:all}.tool-button:hover{background-color:#888}.tool-button--pen{background:#444 url(data:image/png;base64,) center no-repeat}.tool-button--stroke{background:#444 url(data:image/png;base64,) center no-repeat}.tool-button--bucket{background:#444 url(data:image/png;base64,) center no-repeat}.tool-button--bucket-all{background:#444 url(data:image/png;base64,) center no-repeat}.tool-button--eraser{background:#444 url(data:image/png;base64,) center no-repeat}.tool-button--color-picker{background:#444 url(data:image/png;base64,) center no-repeat}.tool-button--selected{border:0.4rem solid gold}.tool-button__tooltip{pointer-events:none}

.color-selection-wrapper{position:relative;width:7.5rem;height:7.5rem}.color-selection-wrapper__inner{width:0;height:0}.color-selection{cursor:pointer;width:4.6rem;height:4.6rem;border:.4rem solid #444}.color-selection:hover{border-color:#888}.color-selection__primary{position:relative;z-index:10}.color-selection__secondary{position:absolute;top:2.3rem;left:2.3rem}.color-selection-arrow{margin-top:.3rem;width:2rem;height:2rem;fill:#666;cursor:pointer}.color-selection-arrow:hover{fill:#d2d2d2}.color-selector{position:absolute;z-index:20}.color-selector--secondary{top:6.8rem;left:2.3rem}

.animation-and-settings-panel{display:flex;flex-direction:column;height:100%;color:#aaa}.sprite-resolution-title{margin-top:.8rem;text-align:center}

.animation-preview{width:25.6rem;height:25.6rem;background:url(data:image/png;base64,) repeat;scale:0.5;cursor:pointer}

.frame-rate-ui-wrapper{display:grid;grid-template-columns:25% 75%;align-items:center;justify-content:center;font-size:1.2rem}.frame-rate-ui-input{cursor:pointer}

.canvasResolutionButton{display:flex;justify-content:center;align-items:center;width:100%;height:2rem;margin-top:0.4rem;border-radius:0.2rem;background-color:gold;-webkit-transition:all 0.4s;transition:all 0.4s}.canvasResolutionButton:hover{background-color:white}.canvasResolutionButton--selected{background-color:white}

.save-as-wrapper{display:flex;justify-content:space-between;align-items:center;width:100%;margin-top:1.6rem}.save-as-button{width:8rem;height:3rem;border-radius:0.2rem;background-color:gold;pointer-events:all;-webkit-transition:all 0.4s;transition:all 0.4s}.save-as-button:hover{background-color:white}.save-as-tooltip{pointer-events:none}

.save-load-wrapper{margin-top:2rem;display:grid;grid-template-columns:1fr 1fr;grid-gap:1rem;gap:1rem}

.save-modal-wrapper{position:fixed;z-index:90;top:0;left:0;display:flex;justify-content:center;align-items:center;width:100vw;height:100vh;background-color:rgba(0,0,0,0.7)}.save-modal-form-wrapper{display:flex;flex-direction:column;justify-content:space-between;align-items:center;width:30rem;height:13rem;color:white}.save-modal-title{color:gold}.save-modal-form{display:flex;flex-direction:column;justify-content:space-evenly;height:13rem}.save-modal-field{width:100%;background-color:#2d2d2d;border:0.1rem solid #666;padding:0.5rem;color:white;box-shadow:0 0 0 500rem #2d2d2d inset;-webkit-text-fill-color:white}.save-modal-button{width:100%;height:3rem;cursor:pointer;background-color:gold;border:none;border-radius:0.2rem}.save-modal-button:hover{background-color:white}

.load-modal-wrapper{position:fixed;z-index:90;top:0;left:0;display:flex;justify-content:center;align-items:center;width:100vw;height:100vh;background-color:rgba(0,0,0,0.7)}.load-modal{display:grid;grid-template-columns:1fr 1fr;grid-auto-rows:4rem;grid-gap:1rem;gap:1rem;padding:2rem;height:80vh;width:80vw;background-color:#1d1d1d;border:0.4rem solid gold;border-radius:0.2rem;overflow-y:scroll}.load-modal::-webkit-scrollbar{-webkit-appearance:none;width:6px}.load-modal::-webkit-scrollbar-thumb{border-radius:2px;background-color:#666}.load-modal::-webkit-scrollbar-thumb{border-radius:2px;background-color:#999;cursor:pointer}.load-modal::-webkit-scrollbar-track{background-color:rgba(50,50,50,0.4)}.load-modal::-webkit-scrollbar-track{background-color:rgba(50,50,50,0.4)}.load-cell{display:flex;align-items:center;justify-content:space-between;height:4rem;padding:0 1rem;border:0.2rem solid #888;border-radius:0.2rem;font-size:1.4rem;text-overflow:ellipsis}.load-cell:hover{cursor:pointer;border-color:gold}.load-cell__shortcut{color:gold;text-transform:capitalize}.load-delete-button{position:relative;margin-left:1rem;width:2rem;height:2rem;opacity:0.3;background-color:#00000000}.load-delete-button:hover{opacity:1}.load-delete-button::before,.load-delete-button::after{position:absolute;left:45%;top:0;content:' ';height:100%;width:0.2rem;background-color:#fff}.load-delete-button::before{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.load-delete-button::after{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}

.save-load-button{height:3rem;border-radius:0.2rem;background-color:gold;pointer-events:all;-webkit-transition:all 0.4s;transition:all 0.4s}.save-load-button:hover{background-color:white}

.frame-panel{height:100%;overflow-y:scroll;scrollbar-color:#292929 #343434}.frame-panel::-webkit-scrollbar{-webkit-appearance:none;width:6px}.frame-panel::-webkit-scrollbar-thumb{border-radius:2px;background-color:#666}.frame-panel::-webkit-scrollbar-thumb{border-radius:2px;background-color:#999;cursor:pointer}.frame-panel::-webkit-scrollbar-track{background-color:rgba(50,50,50,0.4)}.frame-panel::-webkit-scrollbar-track{background-color:rgba(50,50,50,0.4)}

.frame-window{display:flex;justify-content:center;align-items:center;position:relative;width:14rem;height:14rem;border-radius:0.4rem;margin:1rem;background-color:#444;cursor:pointer}.frame-window:hover{background-color:#888}.frame-window:hover .frame-delete,.frame-window:hover .frame-duplicate{visibility:visible}.frame-window--selected{background-color:gold}.frame-window--selected:hover{background-color:gold}.frame-window--being-dragged{opacity:0.001}.frame-window--dragged-over-bottom{box-shadow:0 0.6rem 0.2rem -0.1rem gold}.frame-window--dragged-over-top{box-shadow:0 -0.6rem 0.2rem -0.1rem gold}.frame-delete,.frame-duplicate{visibility:hidden;width:4rem;height:4rem;pointer-events:visible}.frame-delete:hover,.frame-duplicate:hover{background-color:#444}.frame-delete__wrapper,.frame-duplicate__wrapper{position:absolute;right:0.6rem;z-index:10;pointer-events:none}.frame-delete{border-radius:0 0 0 0.4rem;background:url(data:image/png;base64,) center no-repeat}.frame-delete__wrapper{top:0.6rem}.frame-duplicate{border-radius:0.4rem 0 0 0;background:url(data:image/png;base64,) center no-repeat}.frame-duplicate__wrapper{bottom:0.6rem}.frame-number{position:absolute;top:0;left:0;z-index:10;display:flex;justify-content:center;align-items:center;width:4rem;height:4rem;border-radius:0 0.4rem 0.4rem;background-color:gold;color:black;font-size:2rem;pointer-events:none}.frame-canvas{-webkit-transform:scale(0.25);transform:scale(0.25);background:url(data:image/png;base64,) repeat;pointer-events:none}

.add-frame-ui{display:flex;align-items:center;justify-content:space-evenly;margin-top:1rem;margin-left:1rem;width:14rem;height:3rem;border-radius:0.4rem;border:#888 solid 3px;background-color:#222;cursor:pointer;pointer-events:all}.add-frame-ui:hover{border-color:gold}.add-frame-ui__tooltip{pointer-events:none;text-align:center}.add-frame-ui__icon{font-size:2.6rem;color:#888;font-weight:bold}.add-frame-ui__text{font-size:1.3rem;color:#888;font-weight:bold}

.customize-shortcuts-button{position:absolute;bottom:2rem;left:2rem;width:3.6rem;height:2.1rem;background:url(data:image/png;base64,) center/cover no-repeat;opacity:0.7;-webkit-transition:all .2s;transition:all .2s}.customize-shortcuts-button:hover{opacity:1}

.customize-shortcuts-modal-wrapper{position:fixed;z-index:90;top:0;left:0;display:flex;justify-content:center;align-items:center;width:100vw;height:100vh;background-color:rgba(0,0,0,0.7)}.customize-shortcuts-modal{display:grid;grid-template-columns:1fr 1fr;grid-auto-rows:4rem;grid-gap:1rem;gap:1rem;padding:2rem;height:80vh;width:80vw;background-color:#1d1d1d;border:0.4rem solid gold;border-radius:0.2rem;overflow-y:scroll}.customize-shortcuts-modal::-webkit-scrollbar{-webkit-appearance:none;width:6px}.customize-shortcuts-modal::-webkit-scrollbar-thumb{border-radius:2px;background-color:#666}.customize-shortcuts-modal::-webkit-scrollbar-thumb{border-radius:2px;background-color:#999;cursor:pointer}.customize-shortcuts-modal::-webkit-scrollbar-track{background-color:rgba(50,50,50,0.4)}.customize-shortcuts-modal::-webkit-scrollbar-track{background-color:rgba(50,50,50,0.4)}.customize-shortcuts-cell{display:grid;grid-template-columns:1fr 1fr;align-items:center;grid-gap:1rem;gap:1rem;height:4rem;padding-left:1rem;border:0.2rem solid #888;border-radius:0.2rem;overflow:hidden;font-size:1.4rem;text-overflow:ellipsis}.customize-shortcuts-cell:hover{cursor:pointer;border-color:gold}.customize-shortcuts-cell__shortcut{color:gold;text-transform:capitalize}.restore-default-shortcuts-button{position:absolute;bottom:3vh;left:50%;-webkit-transform:translate(-50%);transform:translate(-50%);padding:1rem;background-color:gold;border-radius:0.2rem;-webkit-transition:all 0.2s;transition:all 0.2s}.restore-default-shortcuts-button:hover{background-color:white}

.register-page{display:flex;justify-content:center;align-items:center;height:calc(100vh - 3.6rem)}

.form-wrapper{width:40rem;height:60rem}.form-title{text-align:center}.input-group{margin:2rem 0}.input-group label{display:block;margin-bottom:0.2rem}.input-group input{width:100%;height:4rem;background-color:#2d2d2d;border:0.1rem solid #666;padding:0 0.5rem;font-size:2rem;color:white;box-shadow:0 0 0 500rem #2d2d2d inset;-webkit-text-fill-color:white}.input-group input:-webkit-autofill::first-line{font-size:2rem}.form-button{width:100%;height:4rem;cursor:pointer;background-color:gold;border:0;border-radius:0.2rem}.form-button:hover{background-color:white}

.login-page{display:flex;justify-content:center;align-items:center;height:calc(100vh - 3.6rem)}

